import util from '../../common/js/util'
import {
    getCate,
    addCategory,
    addBrandById,
    deleteCateById,
    deleteBrandById
} from '../../api/cateGoryApi'

export default {
    getList() {
        getCate().then(res => {
            this.list = res.data
            if (res.data !== null) {
                for (let i = 0; i < res.data.length; i++) {
                    for (let j = 0; j < res.data[i].Category.length; j++) {
                        let obj = {
                            category: res.data[i].Name,
                            brand: res.data[i].Category[j].Name
                        }
                        window.sessionStorage.setItem('key_' + res.data[i].Category[j].Id, JSON.stringify(obj));
                    }
                }
                window.sessionStorage.setItem('pickValue', JSON.stringify(this.list))
            }
        })
    },
    //
    addcate() {
        if (this.list !== null) {
            for (var i = 0; i < this.list.length; i++) {
                if (this.list[i].Name == this.addForm.name) {
                    this.$notify.error({
                        title: '',
                        message: '',
                        position: 'bottom-right'
                    });
                    return true
                }
            }
        }

        let para = {
            name: this.addForm.name
        };
        addCategory(para).then(res => {
            this.$notify.success({
                title: '',
                message: '',
                position: 'bottom-right'
            });
            window.sessionStorage.clear();
            this.getList()
        }).catch(err => {
            this.$notify.error({
                title: '',
                message: '',
                position: 'bottom-right'
            });
        });

        this.visible = false
    },

    //Id
    addBrandId(id) {
        this.bId = id
        this.showBrandDialogVisible = true
    },
    //
    addBrand() {
        let para = {
            pid: this.bId,
            name: this.addBrandsForm.name
        }
        addBrandById(para).then(() => {
            this.$notify.success({
                title: '',
                message: '',
                position: 'bottom-right'
            });
            window.sessionStorage.clear();
            this.getList()
        }).catch(err => {
            this.$notify.error({
                title: '',
                message: '',
                position: 'bottom-right'
            });
        })
        this.showBrandDialogVisible = false
    },



    // 
    deleteBrand(id) {
        this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(() => {
            deleteCateById(id).then(res => {
                console.log(res)
                window.sessionStorage.clear();
                this.getList()
                this.$notify.success({
                    title: '',
                    message: '',
                    position: 'bottom-right'
                });
            })
        })
    },

    // 
    removeBrand(id) {
        deleteBrandById(id).then(
            res => {
                window.sessionStorage.clear();
                this.getList(),
                    this.$notify.success({
                        title: '',
                        message: '',
                        position: 'bottom-right'
                    });
            }
        )
    },

    //----------------------------------------------------------------------------------
    // 
    formClose(formName) {
        if (this.$refs[formName] !== undefined) {
            this.$refs[formName].resetFields();
        }
    },
}



